.programm-offer {
  width: 320px;
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    width: 768px;
    margin-bottom: 45px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 auto;
    width: 1220px;
    display: flex;
    margin-bottom: 71px;
  }
}

.programm-offer__weight-change {
  background-color: @basic-grey;
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 708px;
  }

  @media (min-width: @desktop-width) {
    width: 570px;
  }
}

// стилизация картинок в блоке о похудении
.programm-offer-weight-loss {
  background-image: url("../img/cat-weight-loss.svg");
  background-repeat: no-repeat;
  background-position: 22px 21px;
  background-size: 50px 53px;

  @media (min-width: @tablet-width) {
    background-position: 446px 81px;
    background-size: auto;
  }

  @media (min-width: @desktop-width) {
    background-position: 52px 41px;
    background-size: 100px 106px;
  }
}

// стилизация картинки в блоке о наборе веса
.programm-offer-weight-gain {
  background-image: url("../img/cat-weight-gain.svg");
  background-repeat: no-repeat;
  background-position: 13px 24px;
  background-size: 67px 50px;

  @media (min-width: @tablet-width) {
    background-position: 412px 91px;
    background-size: auto;
  }

  @media (min-width: @desktop-width) {
    background-position: 33px 47px;
    background-size: 134px 100px;
  }
}

.programm-offer__wrapper {
  padding: 30px 33px 18px 20px;

  @media (min-width: @tablet-width) {
    padding: 77px 62px 56px 52px;
  }

  @media (min-width: @desktop-width) {
    padding: 77px 50px 50px 50px;
  }
}

.programm-offer__weight-change:not(:last-child) {
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    margin-bottom: 30px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
  }
}

.programm-offer__title {
  font-family: "Oswald", sans-serif;
  margin: 0;
  margin-bottom: 29px;
  margin-left: 71px;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;

  text-transform: uppercase;
  color: @black;

  position: relative;

  @media (min-width: @tablet-width) {
    font-size: 36px;
    line-height: 36px;
    margin-left: 0;
    margin-bottom: 24px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 73px;
    margin-left: 214px;
  }
}

.programm-offer__description {
  margin: 0;
  padding: 0;

  font-family: "Arial", sans-serif;

  font-size: 14px;
  line-height: 18px;

  color: @dark-grey;
  margin-bottom: 38px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
    margin-right: 254px;
    margin-bottom: 52px;
  }

  @media (min-width: @desktop-width) {
    margin: 0;
    margin-bottom: 23px;
  }
}

.programm-offer__link {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: @black;

  text-transform: uppercase;
  text-decoration: none;
  position: relative;


  &::before {
    content: "";
    position: absolute;
    width: 24px;
    height: 12px;
    background-image: url("../img/arrow-black.svg");
    background-repeat: no-repeat;
    top: 6px;
    left: 100px;
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;

    padding: 4px 48px 4px 0;

    &::before {
      top: 12px;
      left: 133px;
    }
  }
}

// стилизация состояний programm-offer__link
.programm-offer__link:hover {
  &::before {
    content: "";
    position: absolute;
    width: 32px;
    height: 12px;
    background-image: url("../img/arrow-black-hover.svg");
    background-repeat: no-repeat;
    top: 8;
    left: 133px;
  }
}

.programm-offer__link:active {
  opacity: 0.3;
}
